import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';

@Component({
  selector: 'app-notice',
  templateUrl: './notice.component.html',
  styleUrls: ['./notice.component.css']
})
export class NoticeComponent implements OnInit {
  Alist = [{id: 0, name: '', control: 'newNotice'}, {id: 2, name: '', control: 'systemUpdate'}];
  Mlist = [{id: 0, name: '', control: 'newMessage'}, {id: 1, name: '', control: 'groupInvite'},
          {id: 2, name: '', control: 'addFriend'}];
  Tlist = [{id: 0, name: '', control: 'tripEdit'},
          {id: 1, name: '', control: 'tripAssistant', note: ''}];

  public form: FormGroup;
  constructor(
    private fb: FormBuilder,
  ) { }

  ngOnInit() {
    this.form = this.fb.group({
      newNotice: false,
      systemUpdate: false,
      newMessage: false,
      groupInvite: false,
      addFriend: false,
      tripEdit: false,
      tripAssistant: false,
    });
  }
  get newNotice() { return this.form.get('newNotice'); }
  get systemUpdate() { return this.form.get('systemUpdate'); }
  get newMessage() { return this.form.get('newMessage'); }
  get groupInvite() { return this.form.get('groupInvite'); }
  get addFriend() { return this.form.get('addFriend'); }
  get tripEdit() { return this.form.get('tripEdit'); }
  get tripAssistant() { return this.form.get('tripAssistant'); }

  onFormSubmit() {
    console.log(JSON.stringify(this.form.value));
  }
  onToggleChange(event) {
    console.log(event, ':', JSON.stringify(this.form.get(event).value));
  }

}
