import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { Location } from '@angular/common';


import { User } from '../models';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { map } from 'rxjs/operators';

@Component({
    selector: 'app-navbar-back',
    templateUrl: './navbarHback.component.html',
    styleUrls: ['./navbarHback.component.css']
})
export class NavbarHbackComponent implements OnInit {
    login$: Observable<boolean>;
    user$: Observable<User>;
    isInRoom: boolean;
    currentUrl: string;
    isHandset$: Observable<boolean>;
    links = [{route: 'assistant', icon: 'home', name: ''}, {route: 'travel', icon: 'edit_road', name: ''},
              {route: 'communication', icon: 'people', name: ''},  {route: 'personal', icon: 'settings', name: ''}];
    constructor(
      private breakpointObserver: BreakpointObserver,
      private location: Location
    ) { }
    ngOnInit() {
      this.isHandset$ = this.breakpointObserver.observe([Breakpoints.Handset, Breakpoints.Tablet]).pipe(map(match => match.matches));
    }
    goBack(): void {
      this.location.back();
    }
}
