import { Component, Inject, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA, MatSnackBar } from '@angular/material';
import { Router } from '@angular/router';
import { EditData } from 'src/app/main/models';
import { UserService } from '../../user/service/user.service';
import { EditDialogComponent } from '../edit-dialog/edit-dialog.component';

@Component({
  selector: 'app-avatar-dialog',
  templateUrl: './avatar-dialog.component.html',
  styleUrls: ['./avatar-dialog.component.css']
})
export class AvatarDialogComponent implements OnInit {
  public form: FormGroup;
  slideimage: string | ArrayBuffer | any[][];
  allimages: any[] = [];
  slidedata = [];
  isimg: boolean;
  constructor(
    public dialogRef: MatDialogRef<EditDialogComponent>,
    @Inject(MAT_DIALOG_DATA)
    public data: EditData,
    private fb: FormBuilder,
    private userService: UserService,
    private snackbar: MatSnackBar,
    private router: Router
  ) { }

  ngOnInit() {
    this.form = this.fb.group({
        avatar: ['', Validators.pattern('^[a-zA-Z0-9-_]{5,64}')]
    });
  }
  get avatar() { return this.form.get('avatar'); }
  register() {
    // this.userService.register(this.form.value)
    //   .subscribe(res => {
    //     if (res) {
    //       this.snackbar.open('', 'OK', { duration: 3000});
    //     } else {
    //       this.snackbar.open('', 'OK', {duration: 3000});
    //     }
    // });
    this.slideimage = [this.allimages];
    this.dialogRef.close(this.slideimage);
    // console.log(this.form.value.name);
    // this.data.name = this.form.value.name;
  }

  imgupload(event) {
    if (event.target.files && event.target.files.length >= 0) {
        let i: number;
        for (i = 0 ; i < event.target.files.length ; i ++) {
            const reader = new FileReader();
            reader.readAsDataURL(event.target.files[i]); // read file as data url
            reader.onload = () => { // called once readAsDataURL is completed
              this.slideimage = reader.result;
              this.allimages.push(this.slideimage);
              this.haveimg();
              console.log(this.slideimage);
            };
        }
    }
  }

//   deleteimg(index: number) {
//     this.allimages.splice(index, 1);
//     console.log('index:', index);
//     this.haveimg();
//     // this.slideimage = '';
//     // if (this.slideimage) {
//     //     this.allimages.splice(index, 1);
//     //     this.slideimage = '';
//     // }
// }

  haveimg() {
      if (this.allimages.length === 0) {
          this.isimg = false;
      } else {
        this.isimg = true;
      }
      console.log(this.isimg);
  }

  onNoClick(): void {
    this.dialogRef.close();
  }
}
