import { SwiperConfigInterface, SwiperNavigationInterface } from 'ngx-swiper-wrapper';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { UtilsService } from '../servcies';
import { NavService } from '../servcies/nav.service';

@Component({
  selector: 'app-assistant',
  templateUrl: './assistant.component.html',
  styleUrls: ['./assistant.component.css']
})
export class AssistantComponent implements OnInit {
  slides = ['../../../assets/a.jpg', '../../../assets/b.jpg', '../../../assets/c.jpg'];
  public config: SwiperConfigInterface = {
    keyboard: true,
    mousewheel: true,
    scrollbar: false,
    direction: 'horizontal',
    slidesPerView: 1,
    // spaceBetween: 300,
    centeredSlides: false,
    navigation: {
      nextEl: '.swiper-button-next.swiper-button-white',
      prevEl: '.swiper-button-prev.swiper-button-white',
      hideOnClick: true,
    },
    autoplay: true,
    pagination: {
      el: '.swiper-pagination',
      clickable: true,
      hideOnClick: false
    },
  };
  constructor(
    private route: ActivatedRoute,
    private nav: NavService,
    private util: UtilsService,
    // private navbar: NavbarComponent
  ) { }

  ngOnInit() {
    this.route.url.subscribe(res => this.nav.setUrl(res[0].path));
    console.log(this.util.isTokenExpired());
    // console.log(this.nav.getUrl());
  }
  public onIndexChange(index: number) {
    console.log('Swiper index: ', index);
  }

}
