import { Component, Input, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material';
import { Router, ActivatedRoute } from '@angular/router';
import { NavService } from 'src/app/main/servcies/nav.service';
import { UserService } from 'src/app/main/user/service/user.service';

@Component({
  selector: 'app-pwd-detail',
  templateUrl: './pwd-detail.component.html',
  styleUrls: ['./pwd-detail.component.css']
})
export class PwdDetailComponent implements OnInit {
  public form: FormGroup;
  checkPwd: boolean;
  constructor(
    private fb: FormBuilder,
    private snackbar: MatSnackBar,
    private userService: UserService,
    private router: Router,
    private route: ActivatedRoute,
    private nav: NavService,
  ) { }

  ngOnInit() {
    this.form = this.fb.group({
      password: ['', Validators.pattern('^[a-zA-Z0-9-_]{5,20}')],
      confirmpwd: ['', Validators.pattern('^[a-zA-Z0-9-_]{5,20}')],
      confirmpwd2: ['', Validators.pattern('^[a-zA-Z0-9-_]{5,20}')],
    });
  }
  get password() { return this.form.get('password'); }
  get confirmpwd() { return this.form.get('confirmpwd'); }
  get confirmpwd2() { return this.form.get('confirmpwd2'); }

  chkPwd() {
    if (this.confirmpwd.value !== this.confirmpwd2.value) {
      this.confirmpwd2.setErrors({ mismatch: true });
    } else {
      this.confirmpwd2.setErrors(null);
    }
  }

  update() {
    console.log(this.form.value);
    const pwd = this.form.get('password').value;
    const newPwd = this.form.get('confirmpwd').value;
    // const newPWD = JSON.stringify({ password: pwd , new_password: newPwd});
    // const newPWD = JSON.parse('{"password":' && pwd && ', "new_password":' && newPwd && '}');
    // console.log(newPWD);
    this.userService.updateUserPwd(this.form.value)
      .subscribe(res => {
        if (res) {
          this.snackbar.open('', 'OK', { duration: 3000});
          // this.router.navigate(['/personal/account']);
          this.userService.logout();
          this.router.navigate(['/user/login']);
        } else {
          this.snackbar.open('', 'OK', {duration: 3000});
        }
    });
  }

}
