import { CopyrightDetailComponent } from './about-tripo/copyright-detail/copyright-detail.component';
import { DataDetailComponent } from './about-tripo/data-detail/data-detail.component';
import { ServiceDetailComponent } from './about-tripo/service-detail/service-detail.component';
import { AboutTripoComponent } from './about-tripo/about-tripo.component';
import { ChatComponent } from './chat/chat.component';
import { NoticeComponent } from './notice/notice.component';
import { PrivateComponent } from './private/private.component';
import { AccountComponent } from './account/account.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PersonalComponent } from './personal.component';
import { ProfileComponent } from './profile/profile.component';


const routes: Routes = [
  {
    path: 'personal',
    children: [
        { path: '', component: PersonalComponent, pathMatch: 'full' },
        { path: 'profile', component: ProfileComponent },
        { path: 'account', component: AccountComponent, pathMatch: 'full' },
        { path: 'private', component: PrivateComponent },
        { path: 'notice', component: NoticeComponent },
        { path: 'chat', component: ChatComponent, pathMatch: 'full' },

        { path: 'about', component: AboutTripoComponent, pathMatch: 'full' },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PersonalRoutingModule { }
