import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material';
import { Router, ActivatedRoute } from '@angular/router';
import { UserInfo } from 'src/app/main/models';
import { NavService } from 'src/app/main/servcies/nav.service';
import { UserService } from 'src/app/main/user/service/user.service';

@Component({
  selector: 'app-email-detail',
  templateUrl: './email-detail.component.html',
  styleUrls: ['./email-detail.component.css']
})
export class EmailDetailComponent implements OnInit {
  public form: FormGroup;
  userInfo: UserInfo[] = [];
  initEmail: string;
  constructor(
    private fb: FormBuilder,
    private userService: UserService,
    private snackbar: MatSnackBar,
    private router: Router,
    private route: ActivatedRoute,
    private nav: NavService,
  ) { }

  ngOnInit() {
    this.form = this.fb.group({
      email: ['', Validators.email]
    });
    this.getEmail();
  }
  get email() { return this.form.get('email'); }

  update() {
    console.log(this.form.value);
    this.userService.updateUserInfo(this.form.value)
      .subscribe(res => {
        if (res) {
          this.snackbar.open('', 'OK', { duration: 3000});
          this.router.navigate(['/personal/account']);
        } else {
          this.snackbar.open('', 'OK', {duration: 3000});
        }
    });
  }

  getEmail() {
    // this.userService.getUserFromServer().subscribe(data => this.userInfo.push(data.user_info));
    this.userService.getUserInfo().subscribe(userInfo => this.userInfo = userInfo);
    this.userService.getUser();
    this.userService.getUserInfo().subscribe(data => {
      this.userInfo = data;
      for (const i of this.userInfo) {
        this.initEmail = i.email;
      }
    });
  }

}
