import { Message } from '../../models';
export const Messages: Message[] = [
  { id: 1, name: 'Emma', avatar: '../../../assets/avatar1.jpeg', content: '', time: '06:11:26 '},
  { id: 2, name: 'Martin', avatar: '../../../assets/avatar2.jpeg', content: '', time: '06:11:26 ' },
  { id: 3, name: 'Amy', avatar: '../../../assets/avatar1.jpeg', content: '', time: '06:11:26 '},
  { id: 4, name: 'John', avatar: '../../../assets/avatar2.jpeg', content: '', time: '06:11:26 ' },
];

@Injectable({
  providedIn: 'root'
})
export class CommunicationService {
  isInRoom = false;
  roomName: string;
  roomId: number;
  constructor(
    private socket: Socket,
    // private http: HttpClient,
    // private headers: HttpHeaders
  ) {
  //   this.headers = new HttpHeaders();
  //   this.headers.set('Content-Type', 'application/json');
   }
//   public sendRoomMessage(message) {
//     this.socket.emit('sendMessage', message);
//   }

//   public getRoomMessages = () => {
//     return Observable.create((observer) => {
//             this.socket.on('sendMessage', (message) => {
//                 observer.next(message);
//             });
//     });
// }

  getRoomMessages() {
    return this.socket
        .fromEvent<any>('msg')
        .pipe(map(data => data.msg));
  }

  sendRoomMessage(msg: string) {
    this.socket
        .emit('msg', msg);
  }

  getMessages(): Observable<Message[]> {
    const messages = of(Messages);
    return messages;
  }

  getMessage(id: number): Observable<Message> {
    // For now, assume that a hero with the specified `id` always exists.
    // Error handling will be added in the next step of the tutorial.
    // tslint:disable-next-line:no-non-null-assertion
    const message = Messages.find(m => m.id === id)!;
    // this.messageService.add(`HeroService: fetched hero id=${id}`);
    return of(message);
  }

  setInroom(name: string, id: number) {
    this.isInRoom = true;
    this.roomName = name;
    this.roomId = id;
  }

  IsInRoom() {
    return this.isInRoom;
  }

  getRoomName() {
    return this.roomName;
  }

  getRoomId() {
    return this.roomId;
  }
}
