import { NavService } from './../servcies/nav.service';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, ParamMap } from '@angular/router';
import { Observable } from 'rxjs';



import { User } from '../models';
import { UserService } from '../user/service/user.service';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { map } from 'rxjs/operators';

@Component({
    selector: 'app-navbar',
    templateUrl: './navbar.component.html',
    styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {
    login$: Observable<boolean>;
    user$: Observable<User>;
    isInRoom: boolean;
    currentUrl: string;
    isHandset$: Observable<boolean>;
    links = [{route: 'assistant', icon: 'home', name: ''}, {route: 'travel', icon: 'edit_road', name: ''},
              {route: 'communication', icon: 'people', name: ''},  {route: 'personal', icon: 'settings', name: ''}];
    constructor(
      private userService: UserService,
      private router: Router,
      private nav: NavService,
      private breakpointObserver: BreakpointObserver
    ) { }
    ngOnInit() {
      this.isHandset$ = this.breakpointObserver.observe([Breakpoints.Handset, Breakpoints.Tablet]).pipe(map(match => match.matches));
        // this.login$ = Observable.of(true);
      this.login$ = this.userService.getLoginStatus();
      this.user$ = this.userService.getCurrentUser();
        // setTimeout(() => {
        //   this.chkURL();
        // }, 1);
        // setInterval(() => {
        //   this.chkURL();
        // }, 1);
    }
    // logout() {
    //     // this.login$ = Observable.of(false);
    //     this.userService.logout();
    //     this.router.navigate(['/user/login']);
    // }
    chkURL() {
      this.currentUrl = this.nav.currentUrl;
      // console.log(this.currentUrl);
    }
}
