import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AboutTripoComponent } from './about-tripo.component';
import { CopyrightDetailComponent } from './copyright-detail/copyright-detail.component';
import { DataDetailComponent } from './data-detail/data-detail.component';
import { ServiceDetailComponent } from './service-detail/service-detail.component';

const routes: Routes = [
  {
    path: 'about',
    children: [
        { path: '', component: AboutTripoComponent, pathMatch: 'full' },
        { path: 'service_detail', component: ServiceDetailComponent },
        { path: 'data_detail', component: DataDetailComponent },
        { path: 'copyright_detail', component: CopyrightDetailComponent },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AboutTripoRoutingModule { }
