import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Message } from 'src/app/main/models';
import { NavService } from '../servcies/nav.service';
import { CommunicationService } from './service/communication.service';

@Component({
  selector: 'app-communication',
  templateUrl: './communication.component.html',
  styleUrls: ['./communication.component.css']
})
export class CommunicationComponent implements OnInit {
  today = new Date();
  nowT = new Date(Date.parse(new Date().toString()));
  nowTStr = this.getNowTH() + ':' + this.getNowTM() + ':' + this.getNowTS() + ' ' + this.getNowT();
  messages: Message[] = [];
  constructor(
    private route: ActivatedRoute,
    private nav: NavService,
    private communication: CommunicationService
  ) { }

  ngOnInit() {
    this.route.url.subscribe(res => this.nav.setUrl(res[0].path));
    this.getMessages();
  }

  getMessages() {
    this.communication.getMessages()
      .subscribe(messages => this.messages = messages);
  }

  getNowTH() {
    let nowTH = this.nowT.getHours();
    if (nowTH > 12) {
      nowTH = nowTH - 12;
    }
    if (nowTH >= 10) {
      return nowTH;
    } else {
      return '0' + nowTH;
    }
  }

  getNowTM() {
    const nowTM = this.nowT.getMinutes();
    if (nowTM >= 10) {
      return nowTM;
    } else {
      return '0' + nowTM;
    }
  }

  getNowTS() {
    const nowTS = this.nowT.getSeconds();
    if (nowTS >= 10) {
      return nowTS;
    } else {
      return '0' + nowTS;
    }
  }

  getNowT() {
    const nowTH = this.nowT.getHours();
    let now = '';
    if (nowTH > 12) {
      now = '';
      return now;
    } else {
      return now;
    }
  }

}
