import { CommunicationService } from './../communication/service/communication.service';
import { NavService } from '../servcies/nav.service';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, ParamMap } from '@angular/router';
import { Observable } from 'rxjs';
import { Location } from '@angular/common';

import { User } from '../models';
import { UserService } from '../user/service/user.service';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { map } from 'rxjs/operators';

@Component({
    selector: 'app-navbar-room',
    templateUrl: './navbar-room.component.html',
    styleUrls: ['./navbar-room.component.css']
})
export class NavbarRoomComponent implements OnInit {
    roomId: number;
    roomName: string;
    isInRoom: boolean;
    isHandset$: Observable<boolean>;
    links = [{route: 'assistant', icon: 'home', name: ''}, {route: 'travel', icon: 'edit_road', name: ''},
              {route: 'communication', icon: 'people', name: ''},  {route: 'personal', icon: 'settings', name: ''}];
    constructor(
      private breakpointObserver: BreakpointObserver,
      private location: Location,
      private Common: CommunicationService

    ) { }
    ngOnInit() {
      this.isHandset$ = this.breakpointObserver.observe([Breakpoints.Handset, Breakpoints.Tablet]).pipe(map(match => match.matches));
      if (this.Common.IsInRoom()) {
          this.roomName = this.Common.getRoomName();
          this.roomId = this.Common.getRoomId();
      }
    }
    goBack(): void {
      this.location.back();
    }
}
