import { PwdDetailComponent } from './pwd-detail/pwd-detail.component';
import { MobileDetailComponent } from './mobile-detail/mobile-detail.component';
import { EmailDetailComponent } from './email-detail/email-detail.component';
import { UsernameDetailComponent } from './username-detail/username-detail.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AccountComponent } from './account.component';
import { NameDetailComponent } from './name-detail/name-detail.component';



const routes: Routes = [
  {
    path: 'account',
    children: [
        { path: '', component: AccountComponent, pathMatch: 'full' },
        { path: 'username', component: UsernameDetailComponent },
        { path: 'name', component: NameDetailComponent },
        { path: 'email', component: EmailDetailComponent },
        { path: 'mobile', component: MobileDetailComponent },
        { path: 'pwd', component: PwdDetailComponent }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AccountRoutingModule { }
