import { LogoutDialogComponent } from './logout-dialog/logout-dialog.component';
import { Member, UserInfo } from './../models/member';
import { Component, Input, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material';
import { ActivatedRoute, Router } from '@angular/router';
import { __values } from 'tslib';
import { IUpload } from '../models';
import { NavService } from '../servcies/nav.service';
import { UserService } from '../user/service/user.service';
import { AvatarDialogComponent } from './avatar-dialog/avatar-dialog.component';
import { EditDialogComponent } from './edit-dialog/edit-dialog.component';

@Component({
  selector: 'app-personal',
  templateUrl: './personal.component.html',
  styleUrls: ['./personal.component.css']
})
export class PersonalComponent implements OnInit {
  name = 'OH HI YO';
  avatar: string;
  member: Member[] = [];
  UserInfo: UserInfo[] = [];
  // userInfo: UserInfo[] = [];
  linklist = [{route: 'account', icon: 'folder', name: ''},
  {route: 'notice', icon: 'campaign', name: ''}, {route: 'chat', icon: 'chat', name: ''},
  {route: 'about', icon: 'info', name: 'Tripo'}];
  userInfo: any[] = [];
  // public form: FormGroup;
  constructor(
    public dialog: MatDialog,
    private route: ActivatedRoute,
    private nav: NavService,
    private userService: UserService,
    private router: Router
    // private fb: FormBuilder,
  ) { }

  ngOnInit() {
    this.route.parent.url.subscribe(res => this.nav.setUrl(res[0].path));
    console.log(this.nav.getUrl());
    // this.userService.getUserFromServer().subscribe(data => this.member.push(data));
    this.getMembers();
    this.getUserInfo();
    // console.log(this.member);
    // this.name = this.member[0].user_info.name;
    // console.log(this.name);
    // this.avatar = '';
  }

  getMembers() {
    // this.userService.getUserFromServer().subscribe(data => this.member.push(data));
    this.userService.getMembers().subscribe(member => this.member = member);
  }

  getUserInfo() {
    // this.userService.getUserFromServer().subscribe(data => this.userInfo.push(data.user_info));
    this.userService.getUserInfo().subscribe(userInfo => this.userInfo = userInfo);
  }

  editDialog() {
    const dialogRef = this.dialog.open(EditDialogComponent, {
      // width: '250px',
      hasBackdrop: true,
      data: {
        name: this.name
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      // console.log('The dialog was closed');
      this.name = result.name;
    });
  }

  avatarDialog() {
    const dialogRef = this.dialog.open(AvatarDialogComponent, {
      width: '600px',
      // height: '700px',
      data: {
        avatar: this.avatar
      }
    });
    dialogRef.afterClosed().subscribe(res => {
        if (res) {
            this.avatar = res[0];
        }
    });
  }

  logoutDialog() {
    const dialogRef = this.dialog.open(LogoutDialogComponent, {
      width: '600px',
      height: '160px',
      data: {
        title: '',
        message: '?',
        buttonText: {
          ok: '',
          cancel: ''
        }
      }
    });
    dialogRef.afterClosed().subscribe((confirmed: boolean) => {
      if (confirmed) {
        this.logout();
      }
    });
  }
  logout() {
    // this.login$ = Observable.of(false);
    this.userService.logout();
    this.router.navigate(['/user/login']);
  }

}
