import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { User } from '../models';
import { UserService } from '../user/service/user.service';

@Component({
  selector: 'app-footbar',
  templateUrl: './footbar.component.html',
  styleUrls: ['./footbar.component.css']
})
export class FootbarComponent implements OnInit {
    login$: Observable<boolean>;
    user$: Observable<User>;
    isHandset$: Observable<boolean>;
    constructor(
      private userService: UserService,
      private router: Router,
      private breakpointObserver: BreakpointObserver
    ) { }
    links = [{route: 'assistant', icon: 'home', name: '', content: true},
            {route: 'travel', icon: 'edit_road', name: '', content: false},
            {route: 'communication', icon: 'people', name: '', content: false},
            {route: 'personal', icon: 'settings', name: '', content: false}];
    activeLink = this.links[0];
    ngOnInit() {
      this.isHandset$ = this.breakpointObserver.observe([Breakpoints.Web]).pipe(map(match => match.matches));
      // this.login$ = Observable.of(true);
      this.login$ = this.userService.getLoginStatus();
      this.user$ = this.userService.getCurrentUser();
    }
    logout() {
        // this.login$ = Observable.of(false);
        this.userService.logout();
        this.router.navigate(['/']);
    }

    routeChange(event) {
      if (event === 'assistant') {
        return this.links[0].content = true,
        this.links[1].content = false,
        this.links[2].content = false,
        this.links[3].content = false;
      }
      if (event === 'travel') {
        return this.links[1].content = true,
        this.links[0].content = false,
        this.links[2].content = false,
        this.links[3].content = false;
      }
      if (event === 'communication') {
        return this.links[2].content = true,
        this.links[1].content = false,
        this.links[0].content = false,
        this.links[3].content = false;
      }
      if (event === 'personal') {
        return this.links[3].content = true,
        this.links[0].content = false,
        this.links[1].content = false,
        this.links[2].content = false;
      }
    }
}

