import { TravelService } from './../../travel/service/travel.service';
import { UserInfo } from './../../models/member';
import { Token, Privacy } from './../../models/user';
import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';

import { User, Response, Member } from '../../models';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';
import { AppConfig } from '../../common';
import { UtilsService } from '../../servcies';

export const Members: Member[] = [];
export const Privacys: Privacy[] = [];
export const UserInfos: UserInfo[] = [];
@Injectable()
export class UserService {
  public token = [];
  loginStatus = new BehaviorSubject<boolean>(false);
  registerStatus = new BehaviorSubject<boolean>(false);
  currentUser = new BehaviorSubject<UserInfo>(null);
  privacy: Privacy[] = [];
  member: Member[] = [];
  userInfo: UserInfo[] = [];
  Token: Token[] = [];
  private headers: HttpHeaders;
  public accessToken: string;
  public refreshToken: string;
  constructor(
    private http: HttpClient,
    private appConfig: AppConfig,
    private utils: UtilsService,
    private travel: TravelService
  ) {
    this.headers = new HttpHeaders({
      'Content-Type': 'application/json',
      'Access-Control-Allow-Origin': this.appConfig.apiUrl,
      // 'https://netcoretri30days.azurewebsites.net',
      'Access-Control-Allow-Methods': 'GET, PUT, POST, DELETE, OPTIONS',
      'Access-Control-Max-Age': '86400',
  });

  }

  registerServer(registerData): Observable<Response> {
    const username = registerData.username.trim();
    const password = registerData.password.trim();
    const gender = registerData.gender.trim();
    const name = registerData.name.trim();
    const email = registerData.email.trim();
    const mobile = registerData.mobile.trim();
    return this.http.post<Response>(this.appConfig.apiUrl + '/register', {
      headers: this.headers,
      username,
      password,
      gender,
      name,
      email,
      mobile,
    });
  }
  register(registerData): Observable<boolean> {
    return this.registerServer(registerData).pipe(
      map(
        (res: Response) => {
          if (res) {
            this.registerStatus.next(true);
            this.currentUser.next(registerData.username);
            // if (registerData.rememberMe) {
            this.utils.writeToken(res.payload);
            // }
            console.log(res);
            return true;
          } else {
            return false;
          }
        },
        (err: HttpErrorResponse) => {
          if (err.error instanceof Error) {
            console.log('client-side error');
          } else {
            console.log('server-side error');
          }
          return of(false);
        }
      )
    );
  }

  loginServer(loginData): Observable<Response> {
    const username = loginData.username.trim();
    const password = loginData.password.trim();
    return this.http.post<Response>(this.appConfig.apiUrl + '/login', {
      headers: this.headers,
      username,
      password,
    });
  }

  login(loginData): Observable<boolean> {
    return this.loginServer(loginData).pipe(
      map(
        (res: Response) => {
          if (res) {
            if (!res.error) {
              this.loginStatus.next(true);
              this.currentUser.next(loginData.username);
              // if (loginData.rememberMe) {
              this.utils.writeToken(res.payload);
              // }
              this.Token.push(res.payload);

              console.log(res);
              this.token.push(res);
              this.travel.setToken(this.token[0].access_token);
              console.log(this.token);
              return true;
            } else {
              return false;
            }
          } else {
            return false;
          }
        },
        (err: HttpErrorResponse) => {
          if (err.error instanceof Error) {
            console.log('client-side error');
          } else {
            console.log('server-side error');
          }
          return of(false);
        }
      )
    );
  }
  logout() {
    this.loginStatus.next(false);
    this.currentUser.next(null);
    this.token = [];
    Members.splice(0, Members.length);
    UserInfos.splice(0, UserInfos.length);

  }
  getLoginStatus(): Observable<boolean> {
    return this.loginStatus;
  }
  getCurrentUser(): Observable<UserInfo> {
    return this.currentUser;
  }
  checkUser(): Observable<boolean> {
    if (!this.utils.isTokenExpired()) {
      this.loginStatus.next(true);
      this.getUser();
      return of(true);
    } else {
      console.log('no token or token is expired');
      this.utils.removeToken();
      return of(false);
    }
  }
  getUserFromServer() {
    // console.log(this.token[0].access_token);
    // if (this.utils.isTokenExpired()) {
    const token = this.token[0].access_token;
    this.headers = new HttpHeaders().set('Authorization', `Bearer ${token}`);
    console.log(this.headers);
    return this.http.get<Member>(this.appConfig.apiUrl + '/self/info', {headers: this.headers});
    // }
    // if (this.utils.isTokenExpired()) {
    //   const token = this.token[0].access_token;
    //   return this.http
    //     .get(this.appConfig.apiUrl + '/user_info')
    //     .pipe(
    //       map((res: Response) => {
    //         if (res.success) {
    //           return res.payload;
    //         } else {
    //           return null;
    //         }
    //       })
    //     );
    // } else {
    //   return of(null);
    // }
  }

  getMembers(): Observable<Member[]> {
    this.getUserFromServer().subscribe(data => this.member.push(data)); // test
    const members = of(Members);
    return members;
  }

  getUserInfo(): Observable<UserInfo[]> {
    this.getUserFromServer().subscribe(data => this.userInfo.push(data.user_info)); // test
    const userInfo = of(UserInfos);
    return userInfo;
  }

  // getMemberInfo() {
  //   this.member = [];
  //   this.getUserFromServer().subscribe(data => this.member.push(data));
  //   this.getMembers().subscribe(member => this.member = member);
  // }

  // getUserInfoDetail() {
  //   this.userInfo = [];
  //   this.getUserFromServer().subscribe(data => this.userInfo.push(data.user_info));
  //   this.getUserInfo().subscribe(userInfo => this.userInfo = userInfo);
  // }

  getUser() {
    this.getUserFromServer().subscribe(
      (res) => {
        this.currentUser.next(res.user_info);
        // UserInfos.push(res.user_info);
      },
      (err: HttpErrorResponse) => {
        if (err.error instanceof Error) {
          console.log('client-side error');
        } else {
          console.log('server-side error');
        }
      }
    );
  }

  getUserPrivacyServer() {
    const token = this.token[0].access_token;
    this.headers = new HttpHeaders().set('Authorization', `Bearer ${token}`);
    console.log(this.headers);
    return this.http.get<Privacy>(this.appConfig.apiUrl + '/self/privacy', {headers: this.headers});
  }

  getUserPrivacy(): Observable<Privacy[]> {
    this.getUserPrivacyServer().subscribe(data => this.privacy.push(data)); // test
    const privacy = of(Privacys);
    return privacy;
  }

  getUserNoticeServer() {
    const token = this.token[0].access_token;
    this.headers = new HttpHeaders().set('Authorization', `Bearer ${token}`);
    console.log(this.headers);
    return this.http.get<Privacy>(this.appConfig.apiUrl + '/self/privacy', {headers: this.headers});
  }

  getUserNotice(): Observable<Privacy[]> {
    this.getUserPrivacyServer().subscribe(data => this.privacy.push(data)); // test
    const privacy = of(Privacys);
    return privacy;
  }

  getUserName(): any {
    let username: string;
    this.getUserFromServer().subscribe(
      (res) => {
        username = res.user_info.username;
        return username;
      }
    );
  }

  getUserEmail(): any {
    let email: string;
    this.getUserFromServer().subscribe(
      (res) => {
        email = res.user_info.email;
        return email;
      }
    );
  }

  updateUserInfoServer(newData): Observable<Response> {
    // const username = newData.username.trim();
    const token = this.token[0].access_token;
    // console.log(username);
    console.log(token);
    this.headers = new HttpHeaders().set('Authorization', `Bearer ${token}`);
    console.log(this.headers);
    return this.http.post<Response>(this.appConfig.apiUrl + '/self/info', newData, {
      headers: this.headers
    });
  }
  updateUserInfo(newData): Observable<boolean> {
    return this.updateUserInfoServer(newData).pipe(
      map(
        (res: Response) => {
          if (res) {
            if (!res.error) {
              this.member = []; // test
              this.userInfo = []; // test
              this.getMembers().subscribe(member => this.member = member); // test
              this.getUserInfo().subscribe(userInfo => this.userInfo = userInfo); // test
              return true;
            } else {
              console.log(res.error);
              return false;
            }
          } else {
            return false;
          }
        },
        (err: HttpErrorResponse) => {
          if (err.error instanceof Error) {
            console.log('client-side error');
          } else {
            console.log('server-side error');
          }
          return of(false);
        }
      )
    );
  }

  updateUserPwdServer(newData): Observable<Response> {
    // const username = newData.username.trim();
    const token = this.token[0].access_token;
    // console.log(username);
    console.log(token);
    this.headers = new HttpHeaders().set('Authorization', `Bearer ${token}`);
    console.log(this.headers);
    return this.http.post<Response>(this.appConfig.apiUrl + '/self/password', newData, {
      headers: this.headers
    });
  }
  updateUserPwd(newData): Observable<boolean> {
    return this.updateUserPwdServer(newData).pipe(
      map(
        (res: Response) => {
          if (res) {
            if (!res.error) {
              // this.getUser();
              return true;
            } else {
              console.log(res.error);
              return false;
            }
          } else {
            return false;
          }
        },
        (err: HttpErrorResponse) => {
          if (err.error instanceof Error) {
            console.log('client-side error');
          } else {
            console.log('server-side error');
          }
          return of(false);
        }
      )
    );
  }

  updateUserPrivacyServer(newData): Observable<Response> {
    // const username = newData.username.trim();
    const token = this.token[0].access_token;
    // console.log(username);
    console.log(token);
    this.headers = new HttpHeaders().set('Authorization', `Bearer ${token}`);
    console.log(this.headers);
    return this.http.post<Response>(this.appConfig.apiUrl + '/self/privacy', newData, {
      headers: this.headers
    });
  }
  updateUserPrivacy(newData): Observable<boolean> {
    return this.updateUserPrivacyServer(newData).pipe(
      map(
        (res: Response) => {
          if (res) {
            if (!res.error) {
              return true;
            } else {
              console.log(res.error);
              return false;
            }
          } else {
            return false;
          }
        },
        (err: HttpErrorResponse) => {
          if (err.error instanceof Error) {
            console.log('client-side error');
          } else {
            console.log('server-side error');
          }
          return of(false);
        }
      )
    );
  }

}
