import { WelcomeComponent } from './welcome/welcome.component';

import { AssistantModule } from './main/assistant/assistant.module';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AssistantComponent } from './main/assistant/assistant.component';
import { CommunicationComponent } from './main/communication/communication.component';
import { CommunicationModule } from './main/communication/communication.module';
import { TravelComponent } from './main/travel/travel.component';
import { TravelModule } from './main/travel/travel.module';


const routes: Routes = [
  { path: '',   redirectTo: '/user', pathMatch: 'full' },
  { path: 'welcome', component: WelcomeComponent},
  { path: 'user', redirectTo: '/user', pathMatch: 'full' },
  { path: 'assistant', component: AssistantComponent },
  { path: 'travel', redirectTo: '/travel', pathMatch: 'full' },
  { path: 'communication', component: CommunicationComponent },
  { path: 'personal', redirectTo: '/personal', pathMatch: 'full' },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes),
    AssistantModule,
    TravelModule,
    CommunicationModule,
    // PersonalModule
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
