import { Component, ElementRef, Input, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material';
import { ActivatedRoute, Router, RouterModule } from '@angular/router';
import * as intlTelInput from 'intl-tel-input';
import { UserService } from '../service/user.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  public form: FormGroup;
  public iti: any;
  @Input() mobiles;
  constructor(
      private fb: FormBuilder,
      private el: ElementRef,
      private userService: UserService,
      private snackbar: MatSnackBar,
      private router: Router
  ) { }

  ngOnInit() {
    this.form = this.fb.group({
      username: ['', Validators.pattern('^[a-zA-Z0-9-_]{5,64}')],
      password: ['', Validators.pattern('^[a-zA-Z0-9-_]{5,20}')],
      name: ['', Validators.pattern('^[a-zA-Z0-9-_]{5,64}')],
      gender: [''],
      email: ['', Validators.email],
      mobile: ['', Validators.pattern('^[0-9]*$')]
    });
    this.mobiles = this.el.nativeElement.querySelector('#mobile');
    this.iti = intlTelInput(
      this.mobiles,
      {
        allowDropdown: true,
        initialCountry: 'tw',
        separateDialCode: true
      }
    );
  }
  get username() { return this.form.get('username'); }
  get password() { return this.form.get('password'); }
  get name() { return this.form.get('name'); }
  get gender() { return this.form.get('gender'); }
  get email() { return this.form.get('email'); }
  get mobile() { return this.form.get('mobile'); }

  register() {
    this.userService.register(this.form.value)
      .subscribe(res => {
        if (res) {
          this.snackbar.open('', 'OK', { duration: 3000});
          this.router.navigate(['/user/login']);
        } else {
          this.snackbar.open('', 'OK', {duration: 3000});
        }
    });
    console.log(this.form.value);
  }
}
